@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Koulen&family=Lato&family=Nunito&family=Playfair+Display:ital@1&family=Prata&family=Raleway:ital,wght@1,100&family=Roboto&family=Roboto+Condensed&family=Teko&display=swap");

.App {
  max-width: 100vw;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px;
  gap: 50px;
}

.MuiCardActions-root{
  justify-content: space-around;
  align-items: center;
}

.Contenido {
  width: 90%;
  height: 70%;
}

.css-cg219i {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.AgregarContainer{
  max-width: 100%;
}

.Agregar {
  font-family: Roboto, sans-serif;
  font-weight: 400;
  font-size: 32px;
  color: #ffffff;
  background-color: black;
  padding: 10px 30px;
  border: solid black 2px;
  box-shadow: rgb(0, 0, 0) 0px 0px 0px 0px;
  border-radius: 50px;
  transition: 1000ms;
  transform: translateY(0);
  display: flex;
  flex-direction: row;
  align-items: center;
  cursor: pointer;
}

.Agregar:hover {
  transition: 1000ms;
  padding: 10px 50px;
  transform: translateY(-0px);
}

.Contenido{
  display: flex;
  gap: 30px;
  max-width: 90%;
  flex-flow: row wrap;
}


.css-anmq6p-MuiTypography-root{
  margin-bottom: 0 !important;
}

.Subcontenido{
  color: white;
  display: flex;
  padding: 20px;
  align-items: center;
  gap: 30px;
  background-color: black;
  border-radius: 10px;
}

.Subcontenido p{
  width: 90%;
  font-family: Roboto, sans-serif;
  font-weight: 800;
  font-size: 32px;
}

.Subcontenido button{
  width: 80px;
  height: 80px;
  border-radius: 10px;
  background-color: black;
  border-style: none;
}

.EditarIcon{
  width: 50%;
  height: 50%;
  color: blue;
}
.EliminarIcon{
  width: 50%;
  height: 50%;
  color: blue;
}


